import axios from 'axios'

export default {
  // Gets all books
  getSong: function (song) {
    return axios.get('/api/songs/' + song)
  },
  addSong: function(song) {
    return axios.post('/api/new', song)
  },
  searchSong: function(song) {
    return axios.post('/api/search/' + song)
  }
}



